import Vue from 'vue'
import VueRouter from 'vue-router'
import ScheduleView from "@/views/CourseSchedulePage/ScheduleView.vue";
import GuidePage from "@/views/CourseSchedulePage/GuidePage.vue";
import ContactPage from "@/views/CourseSchedulePage/ContactPage.vue";

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'home',
    component: ScheduleView
  },
  {
    path: '/guide',
    name: 'guide',
    component: GuidePage
  },
  {
    path: '/',
    redirect: '/home'
  },{
  path: '/contact',
    name: 'contact',
    component: ContactPage

  }
]

const router = new VueRouter({
  routes
  // ,
  // mode: "hash"//?
})

export default router
