class Transport {
    static data = {
        list: []
    }
    //list的格式[[第一组，{className:'',teacherName:''}],[第二组]]
    static addData(group) {
        this.data.list.push(group);
    }

    static clearData() {
        this.data.list=[];
    }

    static getData() {
        return this.data;
    }
}
export default Transport;