<template>
    <div class="homeWrap">
        <el-container>
            <el-header style="font-size: 40px">手动排课系统</el-header>
            <el-container style="border: 1px solid #eee">

                <!--                左边-->
                <el-aside width="200px">
                    <el-menu :default-openeds="['1']">
                        <el-submenu index="1">
                            <template slot="title"><i class="el-icon-message"></i>选课助手</template>
                            <el-menu-item-group>
                                <el-menu-item index="1-1">
                                    <router-link to="/guide" tag="div">
                                        选课指南
                                    </router-link>
                                </el-menu-item>
                                <el-menu-item index="1-2">
                                    <router-link to="/home" tag="div">
                                        排课系统
                                    </router-link>
                                </el-menu-item>
                                <el-menu-item index="1-3">
                                    <router-link to="/contact" tag="div">
                                        我要反馈
                                    </router-link>
                                </el-menu-item>
                            </el-menu-item-group>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <el-container class="formStyle">
                    <el-row>
                        <div style="display: flex; justify-content: center; align-items: center;">
                        <el-card>
                            诚挚招募擅长前端开发（Vue/React等皆可）的同学帮助我们一起完成这个网页！<br>
                            如有其它所长也欢迎联系我们！<br>
                            联系方式：frostyhecolin@qq.com<br>
                            妮可排课系统 demo Version<br>
                        </el-card></div>
                        <br>
                        <el-form ref="responseForm" :model="responseForm" label-width="200px"
                                 :rules="responseFormRules">
                            <el-form-item label="联系方式" prop="connect">
                                <el-input v-model="responseForm.connect"></el-input>
                            </el-form-item>
                            <el-form-item label="反馈" prop="response">
                                <el-input type="textarea" v-model="responseForm.response"
                                          placeholder="请在此输入您的反馈" :rows="20" class="inputStyle"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="submitResponse">发送</el-button>
                                <el-button @click="resetResponseForm">重置</el-button>
                            </el-form-item>
                        </el-form>
                    </el-row>
                </el-container>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import axios from "axios";
import utils from "@/views/Utils/DeepClone";
import {url} from "@/views/Utils/DeepClone";//后端ip
export default {
    name: "ContactPage",
    data() {
        return {
            responseForm: {
                connect: '',
                response: ''
            },
            responseFormRules: {
                connect: [{required: true, message: '请输入联系方式', trigger: 'blur'}],
                response: [{required: true, message: '请输入反馈', trigger: 'blur'}]
            }
        }
    },
    methods: {
        resetResponseForm() {
            this.$refs['responseForm'].resetFields();
        },
        alertSendFailed() {
            this.$alert('发送失败', '发送失败', {
                confirmButtonText: '确定',
                type:'warning'
            });
        },
        submitResponse() {
            let submitSuccessful = true;
            this.$refs['responseForm'].validate((valid) => {
                if (!valid) {
                    submitSuccessful = false;
                    return false;
                } else {
                    return true;
                }
            });
            if (!submitSuccessful) {
                this.alertSendFailed();
                return;
            }
            return axios.post(
                url + "/sendResponse", utils.request(this.responseForm)
            ).then(response => {
                let code = response.data.code;
                if (code === 1) {
                    this.$alert('发送成功', '发送成功', {
                        confirmButtonText: '确定',
                        // eslint-disable-next-line no-unused-vars
                    });
                }
                // eslint-disable-next-line no-unused-vars
            }).catch(err => {
                this.alertSendFailed();
            })
        }
    }
}
</script>

<style scoped>
.formStyle {
    margin: 10px;
}

.inputStyle {
    width: 50vw;
}
</style>