<template>
    <div class="homeWrap">
        <el-container>
            <el-header style="font-size: 40px">手动排课系统</el-header>
            <el-container style="border: 1px solid #eee">
                <!--                左边-->
                <el-aside width="200px">
                    <el-menu :default-openeds="['1']">
                        <el-submenu index="1">
                            <template slot="title"><i class="el-icon-message"></i>选课助手</template>
                            <el-menu-item-group>
                                <el-menu-item index="1-1">
                                    <router-link to="/guide" tag="div">
                                        选课指南
                                    </router-link>
                                </el-menu-item>
                                <el-menu-item index="1-2">
                                    <router-link to="/home" tag="div">
                                        排课系统
                                    </router-link>
                                </el-menu-item>
                                <el-menu-item index="1-3">
                                    <router-link to="/contact" tag="div">
                                        我要反馈
                                    </router-link>
                                </el-menu-item>
                            </el-menu-item-group>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <!--                中间-->
                <el-container class="homeWrap">
                    <!--                    课程表-->
                    <el-main>
                        <!--                    查找表单-->
                        <el-form :inline="true" :model="checkForm" class="demo-form-inline">
                            <el-form-item label="课程名称（教学班）">
                                <el-input v-model="checkForm.courseName"></el-input>
                            </el-form-item>
                            <el-form-item label="教师名称">
                                <el-input v-model="checkForm.teacherName"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="onCheck">查询</el-button>
                                <el-button type="primary" @click="addToCandidate">添加候选</el-button>
                                <el-button type="primary" @click="showCandidate">查看候选</el-button>
                            </el-form-item>
                        </el-form>
                        <!--                    总课程表格-->
                        <div class="normalContainer">
                            <el-table :data="courseList" border @selection-change="handleSelectionChange"
                                      ref="courseTable" max-height="550px"
                                      fit>
                                <el-table-column type="selection" fixed="left"
                                                 :selectable="checkSelectable"></el-table-column>
                                <el-table-column prop="className" label="教学班">
                                </el-table-column>
                                <el-table-column prop="courseCode" label="课程代码">
                                </el-table-column>
                                <el-table-column prop="teachingLanguage" label="授课语言">
                                </el-table-column>
                                <el-table-column prop="teachers" label="授课教师">
                                </el-table-column>
                                <el-table-column prop="timetable" label="上课信息">
                                </el-table-column>
                                <!--                        <el-table-column prop="targetStudent" label="目标学生">-->
                                <!--                        </el-table-column>-->
                                <!--                        <el-table-column prop="restriction" label="选课限制">-->
                                <!--                        </el-table-column>-->
                                <el-table-column prop="department" label="开课学院">
                                </el-table-column>
                            </el-table>
                        </div>
                        <!--                    分页-->
                        <br>
                        <el-pagination
                                background
                                layout="total, sizes, prev, pager, next"
                                :page-sizes="[10, 20, 50, 100]"
                                :page-size="20"
                                @size-change="handlePageSizeChange"
                                @current-change="handleCurrentPageChange"
                                @prev-change="handleCurrentPageChange"
                                @next-click="handleCurrentPageChange"
                                :total="total">
                        </el-pagination>
                    </el-main>
                    <!--                    &lt;!&ndash;                    左右分割线&ndash;&gt;-->
                    <!--                    <el-divider direction="vertical" class="el-divider&#45;&#45;vertical"></el-divider>-->
                    <!--                    备选栏-->
                    <el-drawer title="候选课表" :visible.sync="candidateTableVisible" :size="'70%'">
                        <el-button type="primary" @click="addTimeSlotVisible=true">添加事件时间</el-button>
                        <span>&nbsp;&nbsp;&nbsp;</span>
                        <el-dialog title="添加每周事件时间"
                                   :visible.sync="addTimeSlotVisible"
                                   width="600px">
                            <el-form :model="addTimeSlotForm" :rules="addTimeSlotRules" ref="addTimeSlotForm">
                                <el-form-item label="事项名称" prop="name">
                                    <el-input v-model="addTimeSlotForm.name" style="width: 75%"></el-input>
                                </el-form-item>
                                <el-row style="display: flex">
                                    <span class="form-center">起止周&nbsp;</span>
                                    <el-form-item prop="startWeek">
                                        <el-input v-model="addTimeSlotForm.startWeek" class="timeSlotFormInput"
                                                  type="number"></el-input>
                                    </el-form-item>
                                    <span class="form-center">&nbsp;-&nbsp;</span>
                                    <el-form-item prop="endWeek">
                                        <el-input v-model="addTimeSlotForm.endWeek" class="timeSlotFormInput"
                                                  type="number"></el-input>
                                    </el-form-item>
                                    <span class="form-center">&nbsp;周&nbsp;</span>
                                    <el-form-item prop="weekType">
                                        <el-select v-model="addTimeSlotForm.weekType" placeholder="请选择周次类型"
                                                   class="timeSlotFormInput">
                                            <el-option label="全" value="all"></el-option>
                                            <el-option label="单周" value="odd"></el-option>
                                            <el-option label="双周" value="even"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-row>
                                <el-row style="display: flex">
                                    <span class="form-center">上课时间&nbsp;</span>
                                    <el-form-item prop="weekDay">
                                        <el-select v-model="addTimeSlotForm.weekDay" placeholder=""
                                                   class="timeSlotFormInput">
                                            <el-option label="星期一" value="0"></el-option>
                                            <el-option label="星期二" value="1"></el-option>
                                            <el-option label="星期三" value="2"></el-option>
                                            <el-option label="星期四" value="3"></el-option>
                                            <el-option label="星期五" value="4"></el-option>
                                            <el-option label="星期六" value="5"></el-option>
                                            <el-option label="星期日" value="6"></el-option>
                                        </el-select>
                                    </el-form-item>
                                    <span class="form-center">&nbsp;第&nbsp;</span>
                                    <el-form-item prop="session">
                                        <el-select v-model="addTimeSlotForm.session" placeholder=""
                                                   class="timeSlotFormInput">
                                            <el-option label="1-2" value="0"></el-option>
                                            <el-option label="3-4" value="1"></el-option>
                                            <el-option label="5-6" value="2"></el-option>
                                            <el-option label="7-8" value="3"></el-option>
                                            <el-option label="9-10" value="4"></el-option>
                                            <el-option label="11" value="5"></el-option>
                                        </el-select>
                                    </el-form-item>
                                    <span class="form-center">&nbsp;节&nbsp;</span>
                                </el-row>

                            </el-form>
                            <span slot="footer" class="dialog-footer">
                                <el-button type="primary" @click="addTimeSlot">确 定</el-button>
                                <el-button @click="resetTimeSlotForm">重 置</el-button>
                            </span>
                        </el-dialog>
                        <el-button type="primary" @click="createSyllabus">创建课表</el-button>
                        <div class="normalContainer">
                            <!--                        备选表-->
                            <div v-for="(table, tableIndex) in candidateTables" :key="tableIndex">
                                <el-input :value="'第'+(tableIndex+1)+'组'" class="candidateTableTitle"
                                          readonly></el-input>
                                <el-table :data="candidateTableData[tableIndex]"
                                          :show-header="false"
                                          class="candidateTable"
                                          fit
                                          border
                                v-show="hideCandidateTable[tableIndex]">
                                    <el-table-column prop="className" label="教学班">
                                    </el-table-column>
                                    <el-table-column prop="teachers" label="授课教师">
                                    </el-table-column>
                                    <el-table-column prop="timetable" label="上课信息">
                                    </el-table-column>
                                    <el-table-column label="操作" width="200px">
                                        <template slot-scope="scope">
                                            <el-button type="text" @click="removeCandidate(scope.row,tableIndex)">删除
                                            </el-button>
                                            <el-dropdown @command="changeCourseGroup">
                                                <span class="el-dropdown-link">
                                                    |移动<i class="el-icon-arrow-down el-icon--right"></i>
                                                </span>
                                                <el-dropdown-menu slot="dropdown">
                                                    <el-dropdown-item
                                                            v-for="(n,targetIndex) in candidateTableData.length"
                                                            :key="targetIndex"
                                                            :command="getChangeData(scope.row,tableIndex,targetIndex)">
                                                        <span> 第{{ n }}组</span></el-dropdown-item>
                                                </el-dropdown-menu>
                                            </el-dropdown>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </div>
                        </div>

                    </el-drawer>
                    <!--                        结果栏-->
                    <el-drawer title="生成结果" :visible.sync="resultVisible" :size="'85%'">
                        <div>
                            <!--                        <table>-->
                            <!--                            <tr>-->
                            <!--                                <th></th> &lt;!&ndash; 空白列 &ndash;&gt;-->
                            <!--                                <th v-for="day in 7" :key="day">{{ getWeekday(day) }}</th> &lt;!&ndash; 日期 &ndash;&gt;-->
                            <!--                            </tr>-->
                            <!--                            <tr v-for="timeSlot in 6" :key="timeSlot">-->
                            <!--                                <td>{{ getTimeSlot(timeSlot) }}</td> &lt;!&ndash; 节次 &ndash;&gt;-->
                            <!--                                <td v-for="day in 7" :key="day">-->
                            <!--                                    &lt;!&ndash;                                    <el-descriptions-item></el-descriptions-item>&ndash;&gt;-->
                            <!--                                    &lt;!&ndash;                                    在考虑换成description&ndash;&gt;-->
                            <!--&lt;!&ndash;                                    :placeholder="getCellContent(day, timeSlot)"&ndash;&gt;-->
                            <!--                                    <el-input type="textarea" :style="getCellStyle(day, timeSlot)"-->
                            <!--                                              :placeholder="getCellContent(day, timeSlot)"-->
                            <!--                                              :autosize="{ minRows: 2, maxRows: 15}"-->
                            <!--                                              class="el-course"-->
                            <!--                                              readonly>-->
                            <!--                                    </el-input>-->
                            <!--                                </td>-->
                            <!--                            </tr>-->
                            <!--                        </table>-->
                        </div>
                        <el-table :data="curSyllabusStr" border fit>
                            <el-table-column type="节次\星期">
                                <template slot-scope="scope">
                                    {{ scope.row[0] }}
                                </template>
                            </el-table-column>
                            <el-table-column v-for="day in 7" :key="day" :label="getWeekday(day)">
                                <template slot-scope="scope">
                                    {{ scope.row[day] }}
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-pagination
                                layout="prev, pager, next"
                                :page-size="1"
                                :total="resultSyllabus.length"
                                @current-change="handleSyllabusPageChange"
                                @prev-change="handleSyllabusPageChange"
                                @next-click="handleSyllabusPageChange">
                        </el-pagination>
                        <el-table :data="curSyllabusListStr" border
                                  ref="courseTable"
                                  fit>
                            <el-table-column prop="className" label="教学班"
                                             :width="flexColumnWidth('教学班', 'className',curSyllabusListStr)">
                            </el-table-column>
                            <el-table-column prop="teachers" label="授课教师"
                                             :width="flexColumnWidth('授课教师', 'teachers',curSyllabusListStr)">
                            </el-table-column>
                            <el-table-column prop="timetable" label="上课时间"
                                             :width="flexColumnWidth('上课时间', 'timetable',curSyllabusListStr)">
                            </el-table-column>
                        </el-table>
                    </el-drawer>
                </el-container>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import axios from "axios";
import utils from "../Utils/DeepClone"
import Transport from "@/views/Transport";
import {url} from "@/views/Utils/DeepClone";//后端ip

const weekDay = ['星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期日'];
const timeSlot = ['1-2节', '3-4节', '5-6节', '7-8节', '9-10节', '11节'];
export default {
    data() {
        const startWeekValid = (rule, value, callback) => {
            if (!this.addTimeSlotForm.startWeek) {
                callback();
            }
            let start = Number(this.addTimeSlotForm.startWeek), end = Number(this.addTimeSlotForm.endWeek);
            if (start > 16 || start < 1) {
                callback(new Error('周次必须位于1-16之间'));
            }
            if (start > end) {
                callback(new Error('结束周必须大于开始周'));
            }
        }
        const endWeekValid = (rule, value, callback) => {
            if (!this.addTimeSlotForm.endWeek) {
                callback();
            }
            let start = Number(this.addTimeSlotForm.startWeek), end = Number(this.addTimeSlotForm.endWeek);
            if (end > 16 || end < 1) {
                callback(new Error('周次必须位于1-16之间'));
            }
            if (start > end) {
                callback(new Error('结束周必须大于开始周'));
            }
            callback();
        }
        return {
            resultVisible: false,
            candidateTableVisible: false,
            candidateTables: [],
            candidateTableData: [],
            hideCandidateTable:[],
            total: 1,
            checkForm: {
                courseName: '',
                teacherName: '',
                page: '1',
                pageSize: '20'
            },
            courseList: [],
            selectRows: [],
            curSyllabus: [],
            curSyllabusStr: [],
            curSyllabusListStr: [],
            createTableForm: {
                candidate: [],
            },
            resultSyllabus: [],
            addTimeSlotVisible: false,
            addTimeSlotForm: {
                name: null,
                weekType: 'all',
                weekDay: null,
                startWeek: 1,
                endWeek: 16,
                session: null
            },
            addTimeSlotRules: {
                name: [
                    {required: true, message: '请输入活动名称', trigger: 'blur'},
                    {min: 1, max: 100, message: '长度在 1 到 100 个字符', trigger: 'blur'}
                ],
                weekType: [
                    {required: true, message: '请选择周次类型', trigger: 'change'}
                ],
                weekDay: [
                    {required: true, message: '请选择星期几', trigger: 'change'}
                ],
                startWeek: [
                    {required: true, message: '请输入起始周次', trigger: 'blur'},
                    {validator: startWeekValid, trigger: 'blur'}
                ],
                endWeek: [
                    {required: true, message: '请输入结束周次', trigger: 'blur'},
                    {validator: endWeekValid, trigger: 'blur'}
                ],
                session: [
                    {required: true, message: '请选择节次', trigger: 'change'},
                ]
            },
        }
    },
    methods: {
        resetTimeSlotForm() {
            this.$refs['addTimeSlotForm'].resetFields();
        },
        addTimeSlot() {
            let submitSuccessful = true;
            this.$refs['addTimeSlotForm'].validate((valid) => {
                if (!valid) {
                    submitSuccessful = false;
                    return false;
                } else {
                    return true;
                }
            });
            if (!submitSuccessful) return;
            this.addTimeSlotVisible = false;
            this.selectedRows = [{
                id: -1,
                className: this.addTimeSlotForm.name,
                timetable: this.getTimeTableStr(this.addTimeSlotForm)
            }];
            this.addToCandidate();
        },
        getTimeTableStr(form) {
            let str = '';
            str += form.startWeek + '-' + form.endWeek;
            if (form.weekType === 'odd') {
                str += '单';
            } else if (form.weekType === 'even') {
                str += '双';
            }
            str += '周,';
            str += weekDay[form.weekDay] + '第';
            str += timeSlot[form.session];
            str += ' ';
            return str;
        },
        getChangeData(row, tableIndex, targetIndex) {
            return {row, tableIndex, targetIndex};
        },
        changeCourseGroup(obj) {
            let row = obj.row, tableIndex = obj.tableIndex, targetIndex = obj.targetIndex;
            let targetTable = this.candidateTableData[targetIndex];
            targetTable.push(row);
            this.removeCandidate(row, tableIndex);
        },
        handleSyllabusPageChange(cur) {
            this.sortSyllabus(this.resultSyllabus[cur - 1]);
        },
        // // eslint-disable-next-line no-unused-vars
        // getCellStyle(day, timeSlot) {
        //     //TODO to be implemented.
        // },
        getCellContent(day, timeSlot) {
            // --day;
            // --timeSlot;
            // if (this.resultVisible === false) {
            //     console.log("好怪，为什么resultVisible===false这个函数还会被加载？");
            //     return "";
            // }
            let str = "";
            let courses = this.curSyllabus[day][timeSlot];
            if (courses === undefined) {
                return "";
            }
            courses.forEach(function (course) {
                str += course.className + "\n";
                str += course.teachers + '\n';
                //TODO 个人的感觉是加入教师而非时间观感更好
                //str += course.timetable + "\n";
            });
            return str;
        },
        getTimeSlot(slot) {
            return timeSlot[--slot];
        },
        getWeekday(day) {
            return weekDay[--day];
        },
        hideCandidate() {//unused
            this.candidateTableVisible = false;
        },
        showCandidate() {
            this.candidateTableVisible = true;
        },
        checkRemoveTable(index) {
            let table = this.candidateTableData[index];
            console.log("组：" + index);
            console.log(table.length);
            if (table.length === 0) {
                this.candidateTables.splice(index, 1);
                this.candidateTableData.splice(index, 1);
            }
        },
        removeCandidate(row, tableIndex) {
            let table = this.candidateTableData[tableIndex];
            const index = table.indexOf(row);
            if (index !== -1) {
                table.splice(index, 1);
                this.checkRemoveTable(tableIndex);
            }
        },
        checkSelectable(row) {
            return row.selectable;
        },
        createSyllabus() {
            this.createTableForm.candidate = this.candidateTableData.map(sub => sub.map(obj => obj.id === -1 ? obj : obj.id));
            return axios.post(url + "/getOwnSyllabus", utils.request(this.createTableForm))
                .then(resp => {
                    this.hideCandidate();
                    this.resultVisible = true;
                    this.resultSyllabus = resp.data.data;
                    this.sortSyllabus(this.resultSyllabus[0]);
                    console.log(this.curSyllabusStr);
                });
        },
        sortSyllabus(courses) {//TODO: this will analyze the timetable field and sort the list, so it requires refactor if the format of field change.
            let height = timeSlot.length;
            let width = weekDay.length;

            //创建一个w*h的数组
            let syllabus = [];
            for (let i = 0; i < width; i++) {
                syllabus.push(new Array(height));
            }
            for (let i = 0; i < width; i++) {
                for (let j = 0; j < height; j++) {
                    syllabus[i][j] = [];
                }
            }

            courses.forEach(function (course) {
                let timeStrs = course.timetable.split(";");
                if (timeStrs === "") return;
                timeStrs.forEach(function (time) {
                    let weekTime = time.split(" ")[0].split(",")[1];
                    let arr = weekTime.split("第");
                    let widthIndex = 0;
                    for (; widthIndex < width; widthIndex++) {
                        if (arr[0] === weekDay[widthIndex]) break;
                    }
                    if (widthIndex === width) {
                        console.log("error! time is:" + time + "course is:" + JSON.stringify(course));
                        return;
                    }
                    let session = arr[1].split("节")[0].split('-');
                    let startIndex = 0;
                    for (; startIndex < height; startIndex++) {
                        if (timeSlot[startIndex].includes(session[0])) break;
                    }
                    let endIndex = 0;
                    for (; endIndex < height; endIndex++) {
                        if (timeSlot[endIndex].includes(session[1])) break;
                    }
                    if (startIndex > endIndex) {
                        console.log("error! startIndex is:" + startIndex + "endIndex is:" + endIndex + "course is:" + JSON.stringify(course));
                        return;
                    }
                    while (startIndex <= endIndex) {
                        //console.log(widthIndex + "," + startIndex);
                        let l = syllabus[widthIndex][startIndex];
                        if (l.includes(course) === false) {
                            l.push(course);
                        }
                        startIndex++;
                    }
                })
            });
            this.curSyllabus = syllabus;

            //课程显示
            //创建一个w*h的数组
            let syllabusStr = [];
            for (let i = 0; i < height; i++) {
                syllabusStr.push(new Array(width));
            }
            for (let i = 0; i < height; i++) {
                for (let j = 0; j < width + 1; j++) {
                    syllabusStr[i][j] = "";
                }
            }
            for (let slot = 0; slot < height; slot++) {
                syllabusStr[slot][0] = timeSlot[slot];
                for (let day = 0; day < width; day++) {
                    syllabusStr[slot][day + 1] += this.getCellContent(day, slot);
                }
            }

            this.curSyllabusStr = syllabusStr;
            this.curSyllabusListStr = courses;//显示选课列表
        },
        addToCandidate() {
            if (this.selectedRows === undefined || this.selectedRows.length === 0) return;
            this.candidateTables.push({});
            this.candidateTableData.push(utils.safeDeepClone(this.selectedRows));
            this.$refs.courseTable.clearSelection();
            this.handleSelectionChange([]);
        },
        handleSelectionChange(rows) {
            this.selectedRows = rows;
        },
        handleCurrentPageChange(newPage) {
            this.checkForm.page = newPage;
            this.onCheck();
        },
        handlePageSizeChange(newSize) {
            this.checkForm.pageSize = newSize;
            this.onCheck();
        },
        onCheck() {
            return axios.post(
                url + "/getCourseList", utils.request(this.checkForm)
            ).then(response => {
                let obj = response.data.data;
                this.courseList = obj.courseList;
                this.total = obj.total;

            }).then(err => {
                console.log(err);
            });
        },
        addTemplate() {
            // eslint-disable-next-line no-unused-vars
            let data = Transport.getData();
            //TODO 完成添加功能
        },

        //列宽自适应撑开

        getMaxLength(arr) {
            return arr.reduce((acc, item) => {
                if (item) {
                    const calcLen = this.getTextWidth(item)
                    if (acc < calcLen) {
                        acc = calcLen
                    }
                }
                return acc
            }, 0)
        },

        getTextWidth(str) {
            let width = 0
            const html = document.createElement('span')
            html.innerText = str
            html.className = 'getTextWidth'
            document.querySelector('body').appendChild(html)
            width = document.querySelector('.getTextWidth').offsetWidth
            document.querySelector('.getTextWidth').remove()
            return width
        },

        flexColumnWidth(label, prop, tableData) {
            // console.log('label', label)
            // console.log('prop', prop)
            // 1.获取该列的所有数据
            const arr = tableData.map(x => x[prop])
            arr.push(label) // 把每列的表头也加进去算
            // console.log(arr)
            // 2.计算每列内容最大的宽度 + 表格的内间距（依据实际情况而定）
            return (this.getMaxLength(arr) + 25) + 'px'
        }
    },
    mounted() {
        this.onCheck();
        this.addTemplate();
    }
}
</script>

<style scoped>
.normalContainer {
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 10px;
    max-width: 100%;
