<template>
    <div class="homeWrap">
        <el-container>
            <el-header style="font-size: 40px">手动排课系统</el-header>
            <el-container style="border: 1px solid #eee">
                <!--                左边-->
                <el-aside width="200px">
                    <el-menu :default-openeds="['1']">
                        <el-submenu index="1">
                            <template slot="title"><i class="el-icon-message"></i>选课助手</template>
                            <el-menu-item-group>
                                <el-menu-item index="1-1">
                                    <router-link to="/guide" tag="div">
                                        选课指南
                                    </router-link>
                                </el-menu-item>
                                <el-menu-item index="1-2">
                                    <router-link to="/home" tag="div">
                                        排课系统
                                    </router-link>
                                </el-menu-item>
                                <el-menu-item index="1-3">
                                    <router-link to="/contact" tag="div">
                                        我要反馈
                                    </router-link>
                                </el-menu-item>
                            </el-menu-item-group>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <el-container class="homeWrap">
                    <el-table
                            :data="articleList"
                            :show-header="false"
                            border
                            class="tableStyle">
                        <el-table-column prop="name" label="文章名称" width="200px"></el-table-column>
                        <el-table-column prop="details" label="详细描述"></el-table-column>
                        <el-table-column fixed="right" label="操作" width="100">
                            <template slot-scope="scope">
                                <el-button @click="view(scope.row)" type="text" size="small">查看</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-container>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import axios from "axios";
import utils from "@/views/Utils/DeepClone";
import {url} from "@/views/Utils/DeepClone";//后端ip
export default {
    // name: "GuidePage",
    data() {
        return {
            articleList: [{id: 1, name: '龙志', details: '这是一个龙志！', link: 'https://www.baidu.com'}, {
                id: 1,
                name: '龙志',
                details: '这是一个龙志！',
                link: 'https://www.bilibili.com'
            }]
        }
    },
    methods: {
        view(row) {
            window.open(row.link);
        },
        getArticleList(){
            return axios.post(
                url + "/getArticleList", utils.request()
            ).then(response => {
                this.articleList = response.data.data;
            }).then(err => {
                console.log(err);
            });
        }
    },
    mounted() {
        this.getArticleList();
    }

}
</script>

<style scoped>
.homeWrap {
    height: 90vh;
}
.tableStyle{
    width: 100%;
    margin: 20px;
}
</style>